import mInput from '../../components/m-input.vue'

export default {
	components: {
		mInput
	},
	data() {
		return {
			targetOne: '',
			targetTwo: '',
			targetThree: '',
			targetFour: '',
			positionTop: 0,
		}
	},
	methods: {
		initPosition() {
			/**
			 * 使用 absolute 定位，并且设置 bottom 值进行定位。软键盘弹出时，底部会因为窗口变化而被顶上来。
			 * 反向使用 top 进行定位，可以避免此问题。
			 */
			this.positionTop = uni.getSystemInfoSync().windowHeight - 100;
		},
		cancelTarget(){
			uni.reLaunch({
				url:"../main/main",
			})
		},
		createTarget() {
			uni.request({
				url: 'http://'+getApp().globalData.urlStr+'/TargetScore',
				method: 'GET',
				data: {
					eventOne: this.targetOne,
					eventTwo: this.targetTwo,
					eventThree: this.targetThree,
					eventFour: this.targetFour,
				},
				success: res => {
					if (res.data.code == 0) {
						uni.showModal({
							content: "提交成功！",
							showCancel: false,
							success: function(res) {
								if (res.confirm) {
									uni.switchTab({
										url: "../main/main",
									})
								}
							}
						})
					}
				},
				fail: () => {},
				complete: () => {}
			});
		}
	},
	onReady() {
		this.initPosition();
	}
}
